import { FileName, FilePath, UploadUserId } from '../value';

export class File {
  readonly name: FileName;
  readonly path: FilePath;
  readonly uploadUserId: UploadUserId;

  constructor(args: {
    name: FileName;
    path: FilePath;
    uploadUserId: UploadUserId;
  }) {
    this.name = args.name;
    this.path = args.path;
    this.uploadUserId = args.uploadUserId;
  }
}
